import React, { useState } from 'react';
import { STRING_TO_URL_CONVERT, HTMLParser } from '../../../../../utils';

const PostContent = ({ item }) => {
    const [reactMore, setReactMore] = useState(false);
    const updateContent = HTMLParser(item.postcontent.replace(/<[^>]+>/g, ''));
    const postcontent = item.networkId == 20 ? item.content : STRING_TO_URL_CONVERT(updateContent);
    const content = Array.isArray(postcontent) ? postcontent.length > 0 ? postcontent[0] : "" : postcontent;
    const notMoreText = content.length > 80 ? true : false;
    const newPostContent = item.networkId == 20 ? HTMLParser(window.slackdown.parse(content, item.slackMember)) : content && String(content) ? HTMLParser(String(content)) : content;

    return (
        <>{notMoreText ?
            <><span className={`mb-1 gridpostContent ${reactMore ? 'trimPostContent2 ' : 'trimPostContent '}`} id={`gridpostContent${item.id}`}>{newPostContent}</span>
                <span className="readMore_btn position-relative text-muted fs-9 fw-semibold cursor-pointer" onClick={(e) => { e.stopPropagation(); setReactMore(!reactMore) }}> {reactMore ? 'Read Less' : '... Read More'}</span></>
            : <p id={`gridpostContent${item.id}`} className="gridpostContent fs-8"> {newPostContent}</p>}
        </>

    )
}

export default PostContent;